"use client";

import { useState, useEffect } from "react";
import { supabase } from "@/lib/supabase";
import Image from "next/image";
import { Search, Package, Filter, Loader2, CheckCircle, Plus } from "lucide-react";
import ProductModal from "@/components/shared/ProductModal";

interface FullMarketplaceClientProps {
  initialProducts: any[];
  stores: { id: string; name: string }[];
}

export default function FullMarketplaceClient({ initialProducts, stores }: FullMarketplaceClientProps) {
  const [products, setProducts] = useState(initialProducts);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);
  const [page, setPage] = useState(1);
  const [search, setSearch] = useState("");
  const [selectedStoreId, setSelectedStoreId] = useState("all");
  const [selectedProduct, setSelectedProduct] = useState<any>(null);
  
  const PAGE_SIZE = 20;

  // --- RE-FETCH WHEN FILTER CHANGES ---
  useEffect(() => {
    const fetchFiltered = async () => {
      setLoading(true);
      setPage(1);

      let query = supabase
        .from("products")
        .select("*, stores(name, slug, subscription_plan)")
        .eq("is_active", true)
        .range(0, PAGE_SIZE - 1)
        .order("created_at", { ascending: false });

      if (selectedStoreId !== "all") {
        query = query.eq("store_id", selectedStoreId);
      }

      const { data } = await query;
      setProducts(data || []);
      setHasMore(data && data.length === PAGE_SIZE ? true : false);
      setLoading(false);
    };

    if (selectedStoreId !== "all" || products !== initialProducts) {
      fetchFiltered();
    }
  }, [selectedStoreId]);

  // --- LOAD MORE ---
  const loadMore = async () => {
    setLoading(true);
    const from = page * PAGE_SIZE;
    const to = from + PAGE_SIZE - 1;

    let query = supabase
      .from("products")
      .select("*, stores(name, slug, subscription_plan)")
      .eq("is_active", true)
      .range(from, to)
      .order("created_at", { ascending: false });

    if (selectedStoreId !== "all") {
      query = query.eq("store_id", selectedStoreId);
    }

    const { data: newProducts } = await query;
    if (newProducts && newProducts.length > 0) {
      setProducts([...products, ...newProducts]);
      setPage(page + 1);
    } else {
      setHasMore(false);
    }
    setLoading(false);
  };

  const filteredProducts = products.filter((p: any) => p.name.toLowerCase().includes(search.toLowerCase()));

  // HANDLE ADD TO CART (Placeholder for now)
  const handleAddToCart = (p: any) => {
    // In a real app with Redux/Context, this would add to global cart.
    // For now, we guide them to the store.
    if(confirm(`Visit ${p.stores.name} to purchase this item?`)) {
        window.location.href = `/${p.stores.slug}`;
    }
  };

  return (
    <div className="max-w-7xl mx-auto px-4 py-6 md:py-8">
      
      {/* --- CONTROLS BAR --- */}
      <div className="sticky top-14 z-30 bg-gray-50/95 backdrop-blur-sm py-4 -mx-4 px-4 border-b border-gray-200 mb-6 transition-all">
        <div className="max-w-4xl mx-auto flex flex-col md:flex-row gap-3">
          <div className="relative flex-1">
            <Search className="absolute left-4 top-3.5 text-gray-400 w-5 h-5" />
            <input 
              placeholder="Search products..." 
              className="w-full pl-12 pr-4 py-3 rounded-xl border border-gray-300 shadow-sm focus:outline-none focus:ring-2 focus:ring-gray-900 bg-white text-base"
              value={search}
              onChange={e => setSearch(e.target.value)}
            />
          </div>

          <div className="relative min-w-[200px]">
             <Filter className="absolute left-4 top-3.5 text-gray-500 w-4 h-4" />
             <select 
               className="w-full pl-10 pr-8 py-3 rounded-xl border border-gray-300 shadow-sm focus:outline-none focus:ring-2 focus:ring-gray-900 bg-white text-gray-700 appearance-none font-medium cursor-pointer"
               value={selectedStoreId}
               onChange={(e) => setSelectedStoreId(e.target.value)}
             >
               <option value="all">All Vendors</option>
               {stores.map(store => (
                 <option key={store.id} value={store.id}>{store.name}</option>
               ))}
             </select>
             <div className="absolute right-4 top-4 pointer-events-none text-gray-400">▼</div>
          </div>
        </div>
      </div>

      {/* --- PRODUCT GRID --- */}
      <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-5 gap-3 md:gap-6">
        {filteredProducts.map((product: any) => (
          <div 
            key={product.id} 
            onClick={() => setSelectedProduct(product)}
            className="bg-white p-2.5 rounded-2xl border border-gray-100 shadow-sm hover:shadow-xl hover:-translate-y-1 transition-all duration-300 cursor-pointer group h-full flex flex-col relative"
          >
            {/* Image */}
            <div className="aspect-square bg-gray-100 rounded-xl mb-3 relative overflow-hidden">
              {product.image_urls?.[0] ? (
                <Image src={product.image_urls[0]} alt="" fill className="object-cover" />
              ) : (
                <div className="w-full h-full flex items-center justify-center text-gray-300"><Package /></div>
              )}
              {product.stores?.subscription_plan === 'premium' && (
                <span className="absolute top-2 left-2 bg-black/70 backdrop-blur-md text-white text-[10px] px-2 py-0.5 rounded-full font-bold shadow-sm">PRO</span>
              )}
              
              {/* --- RESTORED PLUS BUTTON --- */}
              <button 
                onClick={(e) => { e.stopPropagation(); handleAddToCart(product); }}
                className="absolute bottom-2 right-2 bg-white text-gray-900 p-2 rounded-full shadow-lg hover:scale-110 active:scale-95 transition z-10"
              >
                <Plus size={18} strokeWidth={3} />
              </button>
            </div>

            {/* Info */}
            <div className="px-1 flex flex-col flex-1">
              <h3 className="font-bold text-gray-900 text-sm truncate">{product.name}</h3>
              <div className="flex items-center gap-1 text-xs text-gray-500 mb-2 truncate">
                {product.stores?.name}
                {product.stores?.subscription_plan === 'premium' && <CheckCircle size={10} className="text-blue-500 shrink-0"/>}
              </div>
              <div className="mt-auto flex items-center justify-between">
                <p className="text-emerald-700 font-extrabold text-sm md:text-base">₦{product.price.toLocaleString()}</p>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* --- LOAD MORE --- */}
      {hasMore && !search && (
        <div className="mt-12 text-center pb-20">
           <button onClick={loadMore} disabled={loading} className="px-8 py-3 bg-white border border-gray-200 text-gray-900 font-bold rounded-xl hover:bg-gray-50 transition shadow-sm flex items-center gap-2 mx-auto">
             {loading ? <Loader2 className="animate-spin" /> : "Load More Products"}
           </button>
        </div>
      )}

      {/* --- MODAL --- */}
      <ProductModal 
        product={selectedProduct} 
        isOpen={!!selectedProduct} 
        onClose={() => setSelectedProduct(null)} 
        onAddToCart={handleAddToCart} 
      />
    </div>
  );
}